import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as compsActions from "../../actions/compsActions";
import {getBanksList} from "../../actions/reportsActions";

import LendingCriteriaPage from "./LendingCriteriaPage";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";

class LendingCriteriaContainer extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
    user : JSON.parse(localStorage.getItem("data"))
  };
}
  componentDidMount() {
    this.props.getBanks(this.state.user.bank);
    this.state.user.is_lender && this.props.actions.getParameterList();
    this.props.actions.getLendingTypes();
    this.props.actions.getLendingCriteriaLists();
    this.props.actions.getRecourseTypes();
    this.props.navActions.updateNavTitle("Lending Criteria");
  }

  render() {
    let successMessage = null;
    if (this.props.lendingCriteria.successToast) {
      successMessage = (
        <Expire closeToast={this.props.actions.closeSuccessToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.lendingCriteria.message}</p>
            <Button
              variant="flat"
              onClick={this.props.actions.closeSuccessToast}
            >
              Done
            </Button>
          </div>
        </Expire>
      );
    }
    const {
      lendingTypes,
      maxTermTypes,
      minDscrTypes,
      maxInterestOnlyTypes,
      prePaymentPenaltyTypes,
      maxInitialTermTypes,
      maxPermAfterConstructionTypes,
      maxAmortizationTypes,
      recourseTypes,
      simpleRateSheetTypes,
      banksBankers,
      currentBank,
      currentBanker
    } = this.props.lendingCriteria;
    return ( this.props.reports.banks.length > 0 &&
      <div>
        <LendingCriteriaPage
          isLCPage={true}
          isQTS={this.state.user.is_qts}
          lendingPrograms={
            this.props.lendingCriteria.lendingList &&
            this.props.lendingCriteria.lendingList.lending_programs
          }
          bankName={
            this.state.user.is_lender &&
            this.state.user.bank.bank_name
          }
          banks={this.props.reports.banks}
          banksBankers={banksBankers}
          currentBank={currentBank}
          currentBanker={currentBanker}
          setCurrentBank={this.props.actions.setCurrentBank}
          setCurrentBanker={this.props.actions.setCurrentBanker}
          getBanksBankers={this.props.actions.getBanksBankers}
          getLendingCriteriaByBank={this.props.actions.getParameterListByBank}
          getLendingCriteriaByContact={this.props.actions.getParameterListByContact}
          deleteLendingCriteria={this.props.actions.deleteLendingCriteria}
          loading={this.props.lendingCriteria.loading}
          authentication={JSON.parse(localStorage.getItem("data"))}
          noLendingList={this.props.lendingCriteria.noLendingList}
          lists={{
            maxTermTypes,
            minDscrTypes,
            maxInterestOnlyTypes,
            prePaymentPenaltyTypes,
            maxInitialTermTypes,
            lendingTypes,
            maxPermAfterConstructionTypes,
            maxAmortizationTypes,
            recourseTypes,
            simpleRateSheetTypes,
          }}
        />
        {successMessage}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    lendingCriteria: state.lendingCriteria,
    title: state.navTitle,
    comps: state.comps,
    reports: state.reports,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    actions: bindActionCreators(lendingCriteriaActions, dispatch),
    compsActions: bindActionCreators(compsActions, dispatch),
    getBanks: bindActionCreators(getBanksList, dispatch),

  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LendingCriteriaContainer);
