import * as types from "./actionTypes";
import config from "../config";
import { ajax,handleAPIError } from "../lib";
import { tokenExpired } from "./authenticationActions";
import { formatRequestBodyParams, getListByType, Navigation } from "../lib";
import { TypesList } from "../lib";

export function getParameterList() {
  return (dispatch) => {
    dispatch({ type: types.LOADING_PARAM_LIST });
    ajax({
      url: `${config.BASE_URL}get-lending`,
      applyAuthHeader: true,
      success: function (response) {
        if (response.status === "E404") {
          dispatch(tokenExpired());
        }
        if (response.status === "204") {
          dispatch({ type: types.NO_PARAM_LIST });
        }
        if (response.status === "E413") {
          dispatch({ type: types.NO_PARAM_LIST });
        } else {
          dispatch({ type: types.LOAD_PARAM_LIST, lendingList: response.data });
        }
      },
    });
  };
}

