export function getLendingCriteriaLists() {
  return (dispatch) => {
    Promise.all([
      fetch(`${config.BASE_URL}lists/maxTerm`),
      fetch(`${config.BASE_URL}lists/minDscr`),
      fetch(`${config.BASE_URL}lists/maxInterestOnly`),
      fetch(`${config.BASE_URL}lists/prePaymentPenalty`),
      fetch(`${config.BASE_URL}lists/maxInitialTerm`),
      fetch(`${config.BASE_URL}lists/maxPermAfterConstruction`),
      fetch(`${config.BASE_URL}lists/maxAmortization`),
      fetch(`${config.BASE_URL}simple-rate-sheet`),
      fetch(`${config.BASE_URL}lending-types`),
    ]).then(function (responses) {
      return Promise.all(responses.map(function (response) {
        return response.json();
      }));
    }).then(function (data) {
      const  [
        maxTerm,
        minDscr,
        maxInterestOnly,
        prePaymentPenalty,
        maxInitialTerm,
        maxPermAfterConstruction,
        maxAmortization,
        simpleRateSheet,
        lendingTypes
      ] = data;
      dispatch({type: types.LOAD_MAX_TERM_TYPES, maxTermTypes: maxTerm.data, });
      dispatch({type: types.LOAD_MIN_DSCR_TYPES, minDscrTypes: minDscr.data, });
      dispatch({type: types.LOAD_MAX_INTEREST_ONLY_TYPES, maxInterestOnlyTypes: maxInterestOnly.data, });
      dispatch({type: types.LOAD_PREPAYMENT_PENALTY_TYPES, prePaymentPenaltyTypes: prePaymentPenalty.data, });
      dispatch({type: types.LOAD_MAX_INITIAL_TERM_TYPES, maxInitialTermTypes: maxInitialTerm.data, });
      dispatch({type: types.LOAD_MAX_PERM_AFTER_CONSTRUCTION_TYPES, maxPermAfterConstructionTypes: maxPermAfterConstruction.data, });
      dispatch({type: types.LOAD_MAX_AMORTIZATION_TYPES, maxAmortizationTypes: maxAmortization.data, });
      dispatch({type: types.LOAD_SIMPLE_RATE_SHEET_TYPES, simpleRateSheetTypes: simpleRateSheet.data, });
      dispatch({type: types.LOAD_LENDING_TYPES,lendingTypes: lendingTypes.data,});
    }).catch(function (error) {
      console.log(error);
    });
  }
}

