export function addLendingCriteria( postData) {
  console.log(postData);
  return (dispatch) => {
    dispatch({
      type: types.LOADING_SPINNER,  message: true,
    });
    ajax({
      url: `${config.BASE_URL}add-lending`,
      applyAuthHeader: true,
      data: formatRequestBodyParams(postData),
      type: "POST",
      success: function (response) {
        dispatch({
          type: types.ADD_LENDING_CRITERIA_SUCCESS,
          message: response.message,
        });
        dispatch({
          type: types.LOADING_SPINNER,  message: false,
        });
      },
      error: function(XMLHttpRequest, textStatus, errorThrown) {
        console.log("Status: " + textStatus, " - Error: " + errorThrown);
        dispatch({
          type: types.LOADING_SPINNER,  message: false,
        });
      }
    });
  };
}

export function updateLendingCriteria(postData) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}edit-lending`,
      applyAuthHeader: true,
      data: formatRequestBodyParams(postData),
      type: "POST",
      success: function (response) {
        dispatch({
          type: types.UPDATE_LENDING_CRITERIA_SUCCESS,
          message: response.message,
        });
        Navigation.go("/lending-criteria");
      },
    });
  };
}
export function deleteLendingCriteria(postData, contactId) {
  return (dispatch, getState) => {
    dispatch({type: types.LOADING_SPINNER,  message: true});
    ajax({
      url: `${config.BASE_URL}delete-lending`,
      applyAuthHeader: true,
      data: formatRequestBodyParams(postData),
      type: "POST",
      success: function (response) {
        dispatch({
          type: types.UPDATE_LENDING_CRITERIA_SUCCESS,
          message: response.message,
        });
        const currentBanker = getState().lendingCriteria.currentBanker.value;
        ajax({
          url: `${config.BASE_URL}contact-lending`,
          data: {contact_id: currentBanker},
          success: function (response) {
            dispatch({type: types.LOAD_PARAM_LIST, lendingList: response.data});
          }
        })
      }
    });
  };
}

export function searchCityOrZip(city_or_zip) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}get-cities`,
      data: { city_or_zip },
      success: function (response) {
        if (response.isResponse) {
          dispatch({
            type: types.GET_CITY_STATES_LIST,
            searchResults: response.data,
          });
        }
        if (response.status === "E403") {
          dispatch(tokenExpired());
        }
      },
    });
  };
}

export function getBanksBankers(bankId) {
  return async (dispatch) => {
    dispatch({
      type: types.LOADING_SPINNER,  message: true,
    });
    dispatch({type: types.LOADING_PARAM_LIST });
    dispatch({
      type : types.SET_BANKS_BANKERS,
      banksBankers : {value: 0,label : "Select a Lenders Contact"}
    });
    await ajax({
      url: `${config.BASE_URL}get-banks-bankers/${bankId}`,
      applyAuthHeader: true,
      success: function (response) {
        dispatch({
          type: types.LOADING_SPINNER,  message: false,
        });
        if(!response.isResponse){
          dispatch({
            type: types.SHOW_TOAST_ERROR,
            success: false,
            message : response.message,
          });
        } else {
          dispatch({
            type : types.SET_BANKS_BANKERS,
            banksBankers : response.data.map((item) => ({
              value: item.contact_id,
              label : item.contact_name
            }))
          })
        }
      },
    }).catch(() => handleAPIError(dispatch, types.GET_BANKS_BANKERS_FAILED));
  };
}
export function closeSuccessToast() {
  return { type: types.CLOSE_LENDING_CRITERIA_SUCCESS };
}

