import React from "react";
import Container from "muicss/lib/react/container";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import ReactSelect from "react-select";
import Modal from "react-modal";
import LendingCriteria from "./LendingCriteria";
import { Link } from "react-router-dom";

class LendingCriteriaPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      banks: [],
      currentBankSelected :  this.props.currentBank,
      currentBankerSelected :  this.props.currentBanker,
    }
  }
  componentDidMount(){
    this.setState({
      banks: this.props.banks.map((item) => ({
        value: item.bank_id,
        label : item.bank_name
      })),
    })
  }
   selectBank (item) {
     this.setState({
       currentBankSelected: item,
       currentBankerSelected: {label : "Select a Lender Contact", value : 0},
     })
     this.props.setCurrentBank(item);
     this.props.getBanksBankers(item.value);
     this.props.setCurrentBanker({label : "Select a Lender Contact", value : 0});
  }
  selectBanker (item) {
    this.setState({
      currentBankerSelected: item
    })
    this.props.setCurrentBanker(item);
    this.props.getLendingCriteriaByContact(item.value);

  }
  render() {
    let addLink = "/lending-criteria/add";
    let List = null;
    if (this.props.lendingPrograms && this.props.lendingPrograms.length) {
      List = this.props.lendingPrograms.map((item, index) => (
        <LendingCriteria
          key={index}
          index={index}
          item={item}
          isLCPage={this.props.isLCPage}
          authentication={this.props.authentication}
          lists={this.props.lists}
          deleteLendingCriteria={this.props.deleteLendingCriteria}
        />
      ));
    }
    return (
      <div>
        {this.props.loading ? (
          <Modal
            className="loading-modal"
            isOpen={this.props.loading}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className={this.props.loading ? "centered-spinner" : "hidden"}>
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        ) : (
          <div>
            <div className="lc-header">
              <Container>
                <h2>
                {this.props.authentication.is_lender && this.props.bankName || ""}
                </h2>
                {this.props.isQTS && (
                  <Row>
                    <Col sm="4">
                      <ReactSelect
                        defaultValue={this.state.currentBankSelected}
                        value={this.state.currentBankSelected}
                        onChange={item => this.selectBank(item)}
                        options={this.state.banks}
                      />
                    </Col>
                    <Col sm="1"></Col>
                    {this.props.banksBankers.length > 0 &&
                      <Col sm="4">
                        <ReactSelect
                          defaultValue={this.state.currentBankerSelected}
                          value={this.state.currentBankerSelected}
                          onChange={item => this.selectBanker(item)}
                          options={this.props.banksBankers}
                        />
                      </Col>
                    }
                  </Row>
              )}
                {(this.props.authentication.is_lender ||
                (this.props.isQTS && this.state.currentBankerSelected.value > 0 )) && (
                  <Link to={addLink}>
                    <div className="add-circle">
                     <i className="material-icons">add</i>
                    </div>
                  </Link>
                )}
              </Container>
            </div>
            {List}
            {this.props.noLendingList &&
              <div>
                <div>
                  <div className="grey-circle">
                    <i className="material-icons">warning</i>
                  </div>
                  <h2 className="no-results">
                    No Lending Criteria For this user
                  </h2>
                </div>
              </div>
            }
          </div>
        )}
      </div>
    );
  }
}

export default LendingCriteriaPage;
